  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    color: #53b5f2;
  }
  
  .headerTwo {
    border-bottom: 1px solid #53b5f2;
    padding-bottom: 10px;
  }
  
  /* Terminal-like effect */
  .container::before {
    content: "> ";
    font-weight: bold;
  }
  
  /* Blinking cursor effect */
  .container::after {
    content: "|";
    animation: blink 1s infinite;
  }
  
  @keyframes blink {
    0%, 100% { opacity: 1; }
    50% { opacity: 0; }
  }
  
  .navigation {
    /* Styles for the navigation section */
    margin-top: 20px;
    text-align: center;
  }
  
  .btnProjects {
    /* Styles for the projects button */
    display: inline-block;
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    text-decoration: none;
    border-radius: 5px;
  }